html {
	height: auto;
	min-height: 100%;
  }
  
  body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	  sans-serif;
	height: auto;
	min-height: 100%;
	background-color: #e5e7eb;
  }
  
  .sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border: 0;
  }

  
  /** Container **/
  #root {
	max-width: 45rem !important;
	margin-right: auto;
	margin-left: auto;
  }
  
  /** Images **/
  img {
	height: auto;
	max-width: 100%;
  }